import React, { useContext } from "react";
import DownHeader from "./components/DownHeader";
import Header from "./components/Header";
import Main from "./components/Main";
import { AuthContext } from "./ContextAPI/Auth";
import "./Home.scss";
import { Link } from "react-router-dom";
import fire from "./fire";
function Home(props) {
  const { currentUser } = useContext(AuthContext);
  return (
    <>
      {currentUser ? (
        <div className="admin-button-wrapper">
          <section>
            <div className="admin-button-container">
              <Link to="/panel" className="link-to-panel">
                <span className="panel-outline">Go to Admin Panel</span>
              </Link>
              <h2
                className="link-to-panel"
                onClick={() => fire.auth().signOut()}
              >
                <span className="panel-outline">Log out</span>
              </h2>
            </div>
          </section>
        </div>
      ) : null}
      <Header />
      <DownHeader />
      <Main news={props.news} setNews={props.setNews} />
    </>
  );
}
export default Home;
