import React from "react";
import "./News.scss";
export default function News(props) {
  const truncate = function (str) {
    return str.length > 300 ? str.substring(0, 300) + "..." : str;
  };
  return (
    <div className="news-wrapper">
      <h2 className="span-title">
        <span className="outline">News</span>
      </h2>
      <ul className="news">
        {props.news.map((data) => {
          return (
            <li className="new" key={data.Title}>
              <div style={{ display: "flex", margin: "auto" }}></div>
              <div className="new-container">
                <div className="flex-text">
                  <h2 className="new-pre-title">{data.Title}</h2>
                  <p style={{ overflow: "hidden", textOverflow: "ellipsis" }}>
                    {truncate(data.Text)}
                  </p>
                  <img
                    style={{ maxWidth: "2em", height: "auto" }}
                    src={encodeURI(
                      `https://avatars.dicebear.com/api/bottts/${data.Title}.svg`
                    )}
                    alt={`iel robotics ${data.Title}`}
                  />
                  <p>{data.Author}</p>
                </div>
                <div style={{ margin: "auto", textAlign: "center" }}>
                  {data.Image?(<img
                    src={data.Image}
                    style={{
                      maxWidth: "10em",
                      height: "auto",
                      borderRadius: "1em",
                      marginLeft: "1em",
                    }}
                    alt={`iel robotics ${data.Title}`}
                  />):null}
                  {data.Url ? (
                    <a
                      className="log-out-button"
                      style={{ margin: "auto 2.5em" }}
                      href={data.Url}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Go to link
                    </a>
                  ) : null}
                </div>
              </div>
            </li>
          );
        })}
      </ul>
    </div>
  );
}
