import {
  Redirect,
  Route,
  Switch,
  HashRouter as Router,
} from "react-router-dom";
import Admin from "./components/admin/Admin.js";
import Home from "./Home.js";
import { AuthProvider } from "./ContextAPI/Auth";
import Panel from "./components/Panel.js";
import PrivateRoute from "./route/PrivateRoute";
import React from "react";
import fire from "./fire";
const db = fire.firestore();
function App() {
  const [news, setNews] = React.useState([]);
  React.useEffect(() => {
    const fetchData = async () => {
      const newsCollection = await db.collection("news").get();
      setNews(
        newsCollection.docs.map((doc) => {
          return doc.data();
        })
      );
    };
    fetchData();
  }, []);
  return (
    <AuthProvider>
      <Router>
        <Switch>
          <PrivateRoute
            exact={true}
            path="/panel"
            component={(props) => (
              <Panel {...props} news={news} setNews={setNews} />
            )}
          />
          <Route
            path="/"
            component={(props)=>(<Home {...props} news={news} setNews={setNews} />)}
            exact={true}
          />
          <Route path="/admin" component={Admin} exact={true} />
          <Route render={() => <Redirect to="/" />} />
        </Switch>
      </Router>
    </AuthProvider>
  );
}

export default App;
