import React, { useState } from "react";
import fire from "../fire";
import FormNews from "./FormNews";
import Modal from "./Modal";
const db = fire.firestore();

const NewsList = (props) => {
  const [isOpen, setIsOpen] = useState(false);
  const [nonLoopData, setNonLoopData] = useState([]);
  return (
    <>
      <Modal
        open={isOpen}
        onClose={() => {
          setIsOpen(false);
        }}
      >
        <FormNews
          news={props.news}
          setNews={props.setNews}
          currentNews={nonLoopData}
          closeModal={() => setIsOpen(false)}
        />
      </Modal>
      <ul className="news">
        {props.news.map((data) => {
          return (
            <li className="new" key={data.Title}>
              <div style={{ display: "flex", margin: "auto" }}>
                <button
                  onClick={(e) => {
                    let news_copy = props.news.filter(
                      (n) => n.Title !== data.Title
                    );
                    db.collection("news").doc(data.Title).delete();
                    props.setNews(news_copy);
                  }}
                  className="log-out-button"
                  style={{ margin: "auto" }}
                >
                  Delete
                </button>
                <button
                  className="log-out-button"
                  style={{ margin: "auto" }}
                  onClick={() => {
                    setNonLoopData(data);
                    setIsOpen(true);
                  }}
                >
                  Update
                </button>
              </div>
              <div className="new-container">
                <div className="flex-text">
                  <h2 className="new-pre-title">{data.Title}</h2>
                  <p style={{ overflow: "hidden", textOverflow: "ellipsis" }}>
                    {truncate(data.Text)}
                  </p>
                  <img
                    style={{ maxWidth: "2em", height: "auto" }}
                    src={encodeURI(
                      `https://avatars.dicebear.com/api/bottts/${data.Title}.svg`
                    )}
                    alt={`iel robotics ${data.Title}`}
                  />
                  <p>{data.Author}</p>
                </div>
                <div style={{ margin: "auto", textAlign: "center" }}>
                  {data.Image ? (
                    <img
                      src={data.Image}
                      style={{
                        maxWidth: "10em",
                        height: "auto",
                        borderRadius: "1em",
                        marginLeft: "1em",
                      }}
                      alt={`iel robotics ${data.Title}`}
                    />
                  ) : null}
                  {data.Url ? (
                    <a
                      className="log-out-button"
                      style={{ margin: "auto 2.5em" }}
                      href={data.Url}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Go to link
                    </a>
                  ) : null}
                </div>
              </div>
            </li>
          );
        })}
      </ul>
    </>
  );
};
const truncate = function (str) {
  return str.length > 300 ? str.substring(0, 300) + "..." : str;
};
export default function NewsPanel(props) {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <div>
      <h2>News</h2>
      <button
        className="log-out-button"
        onClick={() => {
          setIsOpen(true);
        }}
      >
        Add new
      </button>
      <Modal
        open={isOpen}
        onClose={() => {
          setIsOpen(false);
        }}
      >
        <FormNews
          news={props.news}
          setNews={props.setNews}
          closeModal={() => setIsOpen(false)}
        />
      </Modal>
      {<NewsList setNews={props.setNews} news={props.news} />}
    </div>
  );
}
